import pageTitle from './../../../../components/page-title/index.vue';
import btn from './../../../../components/btn/index.vue';
import inputText from './../../../../components/input-text/index.vue';
import inputSelect from './../../../../components/input-select/index.vue';
import countSelector from './../../../../components/count-selector/index.vue';
import dateSelector from './../../../../components/date-selector/index.vue';
import popup from './../../../../components/popup/index.vue';
export default {
  components: {pageTitle, btn, inputText, inputSelect, countSelector, dateSelector, popup},
  data () {
    return {
      title: ' ',
      pageTitle: '    ',
      errorMessage: '',
      showRememberMessage: false,
      showBackMessage: false,
      disabledDates: {
        to: new Date(Date.now() - 24*60*60*1000), //  
        from: new Date(Date.now() + 13 * 24*60*60*1000), //  
        dates: [] //,       
      },
      highlighted:  {
        from: new Date(), //  
        to: new Date(Date.now() + 14 * 24*60*60*1000), //   
      },
      form: {
        waterBig: {value: 1, price: 0},
        bottleBig: {value: 0, price: 0},
        pump: {value: 0, price: 0},
        waterSmall: {value: 0, price: 0},
        date: new Date(),
        time: 0,
        time_text: '',
        phone: '',
        comment: '',
        address: {}
      }
    }
  },
  computed: {
    config() {
      return this.$root.config.screens.newOrder;
    },
    /**
     *    
     */
    districts() {
        return this.$store.state.address.districts;
    },
    /**
     *    
     * @returns {Object}
     */
    district() {
      if (!this.districts || !this.districts.length) return {
          id: 1,
          name: '-',
          price_big: 150.0,
          price_small: 55.0,
          price_bottle: 450,
          price_pump: 650
      };
      return this.districts.find(item => parseInt(item.id) === parseInt(this.form.address.district));
    },
    /**
     *    ,
     *       
     * @returns {Array}
    */
    times() {
      let result = [],
          notDefaultTimes = [],
          //  ,   ,   ,    ,  
          schedule = this.schedule || this.notDefaultSchedule;
      if (!this.form.date || !(this.form.date instanceof Date) || !schedule || !schedule.length) return result;

      let DayNumber = this.form.date.getDay(),
          Day = this.days[DayNumber];

      //    
      let key = 0;
      if (this.schedule && this.schedule.length) this.schedule.map(item => {
        if (parseInt(item.type) === 1 && item.day === Day) item.times.map(time => {
            result.push({id: key, value: time, access_time: item.access_time});
            key++;
          });
      });

      //   
      notDefaultTimes = []; key = 0;
      if (this.notDefaultSchedule && this.notDefaultSchedule.length)
        this.notDefaultSchedule.map(item => {
          //    ,   ,  
          if (parseInt(item.type) === 0 && this.moment(this.form.date).format('YYYY-MM-DD') === item.date) {
            if (item.times && item.times.length && parseInt(item.weekend) === 0) item.times.map(time => {
                  notDefaultTimes.push({id: key, value: time, access_time: item.access_time});
                  key++;
              });
          }
        });
      if (notDefaultTimes.length) result = Object.assign([], notDefaultTimes);

      return result;
    },
    /**
     * ,      
     *        
     * @returns {boolean}
    */
    isWeekend() {
      let result = false;
        if (this.notDefaultSchedule && this.notDefaultSchedule.length) this.notDefaultSchedule.map(item => {
            //    ,      ,     true
            if (parseInt(item.type) === 0 && this.moment(this.form.date).format('YYYY-MM-DD') === item.date && parseInt(item.weekend) === 1) result = true;
            //    
            if (parseInt(item.weekend) === 1) this.disabledDates.dates.push( this.moment(item.date).toDate() );
          });
      return result;
    },
    /**
     *      ,   (['sunday', 'monday', 'tuesday', ...])
     * @returns {Array}
    */
    days() {
      return this.$store.state.order.days;
    },
    /**
     *   
     * @returns {Object}
    */
    order() {
      return this.$store.state.order.new_order;
    },
    /**
     *     ,
     *   {'region_id_1': [{schedule_1},{schedule_2}], 'region_id_2': [{schedule_2},{schedule_3}]}
     * @returns {computed.scheduleByArea|{}|store.state.scheduleByArea}
     */
    scheduleByArea() {
      return this.$store.state.address.scheduleByArea;
    },
    /**
     *   ,      
     *   [{schedule_1},{schedule_2}]
     * @returns {Array|boolean|undefined}
     */
    schedule() {
      if (!this.form || !this.form.address || !this.form.address.district) return;
      return this.scheduleByArea[this.form.address.district];
    },
    /**
     *     ,
     *   {'region_id_1': [{schedule_1},{schedule_2}], 'region_id_2': [{schedule_2},{schedule_3}]}
     * @returns {computed.scheduleByArea|{}|store.state.scheduleByArea}
     */
    notDefaultScheduleByArea() {
        return this.$store.state.address.notDefaultScheduleByArea;
    },
    /**
     *   ,      
     *   [{schedule_1},{schedule_2}]
     * @returns {Array|boolean|undefined}
     */
    notDefaultSchedule() {
        if (!this.form || !this.form.address || !this.form.address.district) return;
        return this.notDefaultScheduleByArea[this.form.address.district];
    },
    /**
     * ,     
     *   ,      ,
     *            
     * @returns {boolean}
     */
    canOrder() {
      this.errorMessage = '';
      if (!this.times || !this.times.length || !this.form.address || this.form.time === undefined || this.isWeekend) return false;
      let selectedTime = this.times.find(item => parseInt(item.id) === this.form.time); //   : {id: 3, value: '15-18', access_time: '12:00'}
      if (!selectedTime) return false;

      let selectedDate = this.moment(this.form.date).format('DD.MM.YYYY'),
          currentTime = this.moment(new Date(), 'DD.MM.YYYY HH:mm'),
          accessTime = this.moment(selectedDate + ' ' + selectedTime.access_time, 'DD.MM.YYYY HH:mm');

      //          ,    
      let result = currentTime.isBefore(accessTime);
      if (!result) {
        let message = this.config.strings.text_time_error_not_delivery.value.split('%time%');
        this.errorMessage = message[0] + selectedTime.access_time + message[1];
      }

      return result;
    }
  },
  watch: {
    'form.waterBig.value'(val) {
      //         ,      
      if (parseInt(val) < parseInt(this.form.bottleBig.value)) this.form.bottleBig.value = val;
    },
    'order'() {
      this.getOrder();
    },
    'form.time'() {
      this.$nextTick(() => this.getDateTimeText());
    },
    'form.date'() {
      //    ,   this.times    
      this.$nextTick(() => {
        this.form.time = undefined; //  ,   
        if (this.times && this.times.length) { //     
          let firstTime = this.times.find(item => !!item); //   
          if (firstTime && firstTime.id !== undefined) this.form.time = firstTime.id;
        }
        this.getDateTimeText();
      });
    },
    'schedule'() {
      this.findWeekends();
    }
  },
  methods: {
    init() {
      this.title     = this.config.strings.title.value;
      this.pageTitle = this.config.strings.pageTitle.value;

      this.$store.commit('setTitle', this.title);
      this.$store.commit('showBackButton');
      this.$store.commit('toggleToolbar', true);
      this.$store.dispatch('order/getNewOrder');
      this.$store.commit('setBackRoute', {name: 'Addresses'});
      this.getOrder();
      this.$store.dispatch('address/getDistricts');
      setTimeout(() => {
        this.isFirstAddressOrder().then(order => {if (!order) this.showRememberMessage = true;});
        this.findGoodDate();
      }, 600);
    },
    /**
     *      ,
     *      
     */
    findGoodDate() {
        this.form.date = new Date();
        for (let i = 1; i <= 14; i++) {
            setTimeout(() => {//setTimeout        
              if (this.canOrder) return;
              if (!this.times || !this.times.length || this.isWeekend)
                this.form.date = new Date(Date.now() + i * 24*60*60*1000);
              else {
                  this.times.map(time => {
                      if (this.canOrder) return;
                      this.form.time = time.id;
                  });
                  //     ,     ,
                  //    
                  if (!this.canOrder) this.form.date = new Date(Date.now() + i * 24*60*60*1000);
              }
            }, 0);
        }
    },
    /**
     *     store
     */
    save() {
      this.$store.commit('order/setNewOrder', this.form);
      this.form.removePumpAndBottle = false;
      this.$store.commit('order/saveOrderToLocal', this.form);
      setTimeout(() => this.$router.push({name: 'CheckOrder'}), 0);
    },
    /**
     *    2   ,    
     */
    findWeekends() {
        //  14      
        let date, day;
        for (let i = 0; i <= 14; i++) {
            date = new Date(Date.now() + i * 24*60*60*1000);
            day = this.days[date.getDay()];
            //       ,      
            if (!this.inDefaultSchedule(date) && !this.inNotDefaultSchedule(date)) this.disabledDates.dates.push(date)
        }
        return this.disabledDates.dates;
    },
    /**
     *        
     * @param {Date} date
     * @returns {boolean|Date}
     */
    inNotDefaultSchedule(date) {
        if (!(date instanceof Date) || !this.notDefaultSchedule || !this.notDefaultSchedule.length) return false;
        let day = this.days[date.getDay()], result = false;
        this.notDefaultSchedule.map(item => {
            //        
            if (this.moment(date).format('YYYY-MM-DD') === item.date) result = date;
        });
        return result;
    },
    /**
     *          
     * @param {Date} date
     * @returns {boolean|Date}
     */
    inDefaultSchedule(date) {
      if (!(date instanceof Date) || !this.schedule || !this.schedule.length) return false;
      let day = this.days[date.getDay()], result = false;
      this.schedule.map(item => {
          //           
          if (item.day === day) result = date;
      });
      return result;
    },
    /**
     *       
     */
    getOrder() {
      if (this.order && JSON.stringify(this.order) !== JSON.stringify(this.form)) this.form = Object.assign({}, this.order);
      //   date -         ,    ,
      //c       ,    
      this.form.date = new Date();
      //this.disabledDates.to   = new Date(Date.now() - 24*60*60*1000);
      //this.disabledDates.from = new Date(Date.now() + 14 * 24*60*60*1000);

      this.disabledDates.to   = new Date();
      this.disabledDates.from = new Date(Date.now() + 14 * 24*60*60*1000);

      this.highlighted.from = new Date();
      this.highlighted.to   = new Date(Date.now() + 14 * 24*60*60*1000);
    },
    /**
     *          
     */
    getDateTimeText() {
      if (this.form.time === undefined) return;
      let time = this.times.find(item => parseInt(item.id) === parseInt(this.form.time));
      time = (time) ? time.value : '09:00 - 12:00';
      this.form.time_text = this.$root.moment(this.form.date).format('DD MMMM') + ', ' + time;
    },
    /**
     * ,        
     */
    isFirstAddressOrder() {
      return this.$store.dispatch('order/getLocalOrderByAddressId', this.form.address.id)
    }
  },
  created: function() {
    this.$nextTick( () => this.init());
  },
}



